import React, { Component } from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { connect } from "react-redux";
import { authActions } from '../redux/actions/';

import { Title, Field, Label, Control, Input, Button } from 'bloomer';

class UserFormPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      data: {
        email: '',
        password: ''
      }
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    const { data } = this.state
    this.setState({
      data: {
        ...data,
        [e.target.name]: e.target.value
      }
    })
  }

  handleSubmit(e) {
    e.preventDefault()
    let data = this.state.data
    this.props.dispatch(authActions.login(data))
  }

  render() {
    const { data } = this.state

    return (
      <Layout>
        <SEO title="Login Page"/>
        <Title>Login Page</Title>
        <form
          method="post"
          onSubmit={e => {
            this.handleSubmit(e)
          }}
        >
        <Field>
          <Label>Email Address</Label>
          <Control>
              <Input
              type="text"
              name="email"
              value={data.email}
              onChange={this.handleChange}
              placeholder='Email Address' />
          </Control>
        </Field>
        <Field>
          <Label>Password</Label>
          <Control>
              <Input
              type="password"
              name="password"
              value={data.password}
              onChange={this.handleChange}
              placeholder='Password' />
          </Control>
        </Field>
        <Field isGrouped>
          <Control>
            <Button isColor='primary' type='submit'>Login</Button>
          </Control>
          <Control>
            <Link to="/user" className="button is-link">Back</Link>
          </Control>
        </Field>
        </form>
      </Layout>
    )
  }

}

const mapStateToProps = ({ user }) => {
  return { user }
}

export default connect(mapStateToProps)(UserFormPage);
